<template>
  <div class="upload">
    <div class="content">
      <div class="top-line">
        <div class="backBtn" @click="$router.go(-1)">返回</div>
      </div>
      <upload-form v-for="(item, index) in formList"
                   v-if="item.id >= 0" :key="item.id" :id="item.id"
                   @updateForm="updateFormList"
                   @subtractForm="subForm(item.id)">
      </upload-form>
      <div class="btn-line">
        <div class="btns" @click="addForm">上传添加</div>
        <div class="btns" @click="submitForm">确定上传</div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import uploadForm from '../../components/mission/upload/uploadForm.vue';

  export default {
    components: {uploadForm},
    data () {
      return {
        nextId: 1,  // 表单识别id
        formList: [{id: 0}], // 上传表单数据,id用作识别
        uploadIndex: 0 // 上传位数
      };
    },
    methods: {
      addForm () {
        this.formList.push({id: this.nextId++}); // 新增表单
      },
      subForm (id) {
        this.formList.forEach((item, index) => {
          if (item.id === id) {
            this.formList.splice(index, 1);
          }
        });  // 删除表单数据
      },
      updateFormList (data, vee, id) {
        this.formList[id].data = data;
        this.formList[id].vee = vee;
      },
      submitForm () {
        for (; this.uploadIndex < this.formList.length; this.uploadIndex++) {
          let form = this.formList[this.uploadIndex];
          if (form && form.vee) {
            // 表单验证
            let formData = form.data;
            let data = {
              address: formData.seleAddress + '-' + formData.detailAddress,
              mobile: formData.phone,
              name: formData.name,
              feature: formData.feature,
              pic_file: formData.img,
              industry: formData.trade
            };
            this.ajaxSubmit(data, this.uploadIndex);
          } else {
            this.$notify.error({
              title: '表单填写错误',
              message: `第${this.uploadIndex + 1}个表单数据填写信息错误`
            });
          }
        }
        this.uploadIndex = 0;
      },
      ajaxSubmit (data, index) {
        Ajax.save({api: 'upload_data'}, data).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.$notify.success({
              title: '上传成功',
              message: `第${index + 1}个数据已经上传成功`
            });
          } else if (data.status === 500) {
            this.$notify.error({
              title: '上传失败',
              message: `第${index + 1}个数据已经上传失败`
            });
          }
          if (this.uploadIndex === this.formList.length) {
            this.$notify.info({title: '上传结束'});
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .upload {
    padding-bottom: 300px;
  }

  .content {
    width: 1200px;
    margin: 0 auto;
  }

  .top-line {
    margin: 100px 0;
    display: flex;
    justify-content: space-between;
    align-content: center;
    .backBtn {
      width: 128px;
      height: 36px;
      border-radius: 10px;
      background-color: @cCyan;
      box-shadow: 4.7px 3.8px 2px 0 rgba(0, 0, 0, 0.27),
      inset 9.7px 20.8px 2px 0 rgba(89, 193, 210, 0.39);
      cursor: pointer;
      text-align: center;
      line-height: 36px;
      font-size: 24px;
      color: @cWhite;
    }
  }

  .btn-line {
    width: 500px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    .btns {
      width: 175px;
      height: 64px;
      background-color: @cCyan;
      cursor: pointer;
      text-align: center;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
  }
</style>



