<template>
  <div class="goodsForm">
    <div class="left">
      <img class="leftImg" :class="{danger: errors.has('图片')}"
           :src="formData.img">
      <div class="btn-line">
        <div class="loadBtn">
          图片上传
          <input type="file" class="imgInp" ref="imgInp" @change="onPhoto"
                 accept="image/jpg,image/jpeg,image/png"
                 v-validate.initial="'required'" name="图片">
        </div>
        <i class="bar">|</i>
        <div class="delBtn" @click="$emit('subtractForm',id)">删除选项</div>
      </div>
    </div>
    <div class="right">
      <form>
        <inp-line placeTxt="商品的名称"
                  v-model="formData.name"
                  v-validate.initial="'required'" name="名称"
                  :class="{danger: errors.has('名称')}">
          <div slot="left" class="inp-left">名称</div>
        </inp-line>
        <inp-line placeTxt="商品的价格"
                  v-model="formData.price"
                  v-validate.initial="'required'" name="价格"
                  :class="{danger: errors.has('价格')}">
          <div slot="left" class="inp-left">价格</div>
        </inp-line>
        <inp-line placeTxt="商品的介绍文字"
                  v-model="formData.introduction"
                  v-validate.initial="'required'" name="介绍"
                  :class="{danger: errors.has('介绍')}">
          <div slot="left" class="inp-left">介绍</div>
        </inp-line>
      </form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import inpLine from '../../components/common/inpLine.vue';

  export default {
    components: {inpLine},
    props: {
      id: {
        type: Number  // form识别id
      }
    },
    mounted () {
      this.initProList();
    },
    data () {
      return {
        formData: {
          name: null, // 名字
          price: null,  // 价格
          introduction: null, // 介绍
          img: null  // 图片
        }
      };
    },
    watch: {
      formData: {
        handler (newVal) {
          this.$emit('updateForm', newVal, !this.errors.any(), this.id);
        },
        deep: true
      }
    },
    methods: {
      onPhoto (index) {
        // 获取图片
        let inp = this.$refs.imgInp;
        if (inp.files && inp.files[0]) {
          // console.log(inp.files[0].size);
          this.condense(inp.files[0], index);
        }
      },
      condense (file, index) {
        // 压缩图片
        let that = this;
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function (e) {
          var img = new Image();
          var width = 250;  // image resize
          var quality = 1;  // image quality
          var canvas = document.createElement('canvas');
          var drawer = canvas.getContext('2d');
          img.src = this.result;
          img.onload = function () {
            canvas.width = width;
            canvas.height = width * (img.height / img.width);
            drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
            let resImg = canvas.toDataURL('image/jpeg', quality);  // 压缩完毕的图片
            that.formData.img = resImg;
          };
        };
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .goodsForm {
    width: 100%;
    margin: 50px 0;
    display: flex;
    justify-content: space-between;
  }

  .left {
    width: 250px;
    img {
      display: inline-block;
      box-sizing: border-box;
      width: 250px;
      height: 250px;
      border: 1px solid @cBorder;
    }
    .danger {
      border: 1px solid red; // 表单验证失败
    }
    .btn-line {
      margin-top: 18px;
      display: flex;
      justify-content: center;
      font-size: 24px;
      color: @cCyan;
      .loadBtn, .delBtn {
        position: relative;
        display: inline-block;
        width: 4em;
        cursor: pointer;
        .imgInp {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
        }
      }
      .bar {
        display: inline-block;
        margin: 0 10px;
      }
    }
  }

  .right {
    flex: 1;
    margin-left: 50px;
    form {
      .inp-left {
        display: inline-block;
        width: 4em;
        text-align: center;
        &::after {
          content: '|';
          margin: 0 5px 0 15px;
        }
      }
      input {
        padding: 0;
      }
      .danger {
        border: 1px solid red; // 表单验证失败
      }
    }
  }
</style>



