<template>
  <div class="itemWrap">
    <div class="img-box">
      <img class="itemImg">
    </div>
    <div class="info-box">
      名称-小当家超市<br/>
      地址-广东省广州市天河区岗顶新村五巷1054号<br/>
      电话-020-35747985<br/>
      行业-超市<br/>
      特色-24小时营业平价生活快销品
    </div>
    <div class="btns-box">
      <div class="btns distance">距离1~1.1km</div>
      <div class="btns navigation">地图导航</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {};
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .itemWrap {
    display: flex;
    margin-bottom: 40px;
  }

  .img-box {
    img {
      display: inline-block;
      width: 320px;
      height: 320px;
    }
  }

  .info-box, .btns-box {
    font-size: 20px;
    margin: auto 0;
    line-height: 40px;
  }

  .info-box {
    width: 630px;
    padding-left: 70px;
  }

  .btns {
    background-color: @cCyan;
    padding-left: 25px;
    padding-right: 25px;
    color: @cWhite
  }

  .distance {
    margin-bottom: 20px;
  }

  .navigation {
    cursor: pointer;
  }
</style>



