<template>
  <div class="informationDetail">
    <div class="content">
      <img class="img" src="123">
      <div class="time">{{time}}</div>
      <div class="divider"></div>
      <div class="detail">{{detail}}</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        detail: '号召力最新活动安排流程',
        time: '2017/03/27'
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .content {
    width: 1200px;
    margin: 0 auto;
  }

  .img {
    width: 120px;
    height: 40px;
  }

  .time {
    width: 100%;
    text-align: right;
    color: @cGray;
    font-size: 18px;
  }

  .detail{
    margin: 30px;
    font-size: 20px;
  }

  .divider {
    width: 100%;
    height: 1px;
    background-color: @cDarkCyan
  }

</style>



