import Mission from '../views/mission/mission.vue';
import Index from '../views/mission/index.vue';
import Get from '../views/mission/get.vue';
import Errs from '../views/mission/errs.vue';
import ErrsDetail from '../views/mission/errsDetail.vue';
import Upload from '../views/mission/upload.vue';
import Search from '../views/mission/search.vue';

export default {
  path: '/mission',
  component: Mission,
  redirect: '/mission/index',
  children: [
    {
      path: 'index',
      component: Index
    },
    {
      path: 'get',
      component: Get
    },
    {
      path: 'errs',
      component: Errs
    },
    {
      path: 'errsDetail/:obj_formData',
      name: 'errsDetail',
      component: ErrsDetail
    },
    {
      path: 'upload',
      component: Upload
    },
    {
      path: 'search',
      component: Search
    }
  ]
};



