import Personal from '../../views/personal/personal.vue';
import Info from './src/info.js'; // 个人信息中心
import Points from '../../views/personal/points.vue';  // 积分提现
// 金币提现
import Bank from '../../views/personal/bank.vue'; // 绑定银行卡

export default {
  path: '/personal',
  component: Personal,
  children: [
    Info,
    {
      // path: 'points/:taskInfo',
      path: 'points/:id/:completed',
      name: 'points',
      component: Points
    },
    {
      path: 'bank',
      component: Bank
    }
  ]
};



