<template>
  <li class="errsItem">
    <div class="content">
      <div class="left">
        <img :src="formData.fields.pic_file" alt="图片错误！">
        <div class="btns-box">
          <div class="btns change" @click="jumpErrsDetail">修改资料</div>
          <div class="bar">|</div>
          <div class="btns del" @click="ajaxDel">删除选项</div>
        </div>
      </div>
      <div class="right">
        <h3 class="name">{{formData.fields.name}}</h3>
        <div class="date">
          发布日期：{{formData.fields.opt_time.split('T')[0]}}
        </div>
        <div class="errTxt">
          <div class="status" :class="status.class">状态：{{status.txt}}</div>
          <div v-if="status.txt === '拒绝'" class="res">
            理由：{{formData.fields.remark}}
          </div>
        </div>
      </div>
    </div>
  </li>
</template>

<script type="text/ecmascript-6">
  const REJECT = -1;
  const REVIEWING = 0;
  const PASS = 1;

  import base64 from 'base-64';
  import utf8 from 'utf8';
  import Ajax from '@/api';

  export default {
    props: {
      formData: {
        type: Object  // 错误信息数据
      }
    },
    data () {
      return {};
    },
    computed: {
      status () {
        // 审核状态
        if (this.formData.fields.status === REJECT) {
          return {class: 'reject', txt: '拒绝'};
        } else if (this.formData.fields.status === REVIEWING) {
          return {class: 'reviewing', txt: '审核中'};
        } else if (this.formData.fields.status === PASS) {
          return {class: 'pass', txt: '通过'};
        }
        return {};
      }
    },
    methods: {
      jumpErrsDetail () {
        // 带参数跳转到详情页面
        let fixed = base64.encode(utf8.encode(JSON.stringify(this.formData)));
        this.$router.push({
          name: 'errsDetail',
          params: {obj_formData: fixed}
        });
      },
      ajaxDel () {
        // 删除错误
        let data = {content_id: this.formData.pk};
        Ajax.save({api: 'delete_upload_data'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify.success({title: '删除成功！'});
            this.$emit('delErrItem', this.formData.pk);
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .errsItem {
    margin: 30px 0;
  }

  .content {
    display: flex;
    justify-content: space-around;
  }

  .left {
    margin-right: 20px;
    text-align: center;
    img {
      display: inline-block;
      box-sizing: border-box;
      width: 190px;
      height: 190px;
      border: 1px solid @cBorder;
      margin-bottom: 20px;
      line-height: 190px;
      font-size: 24px;
    }
    .btns-box {
      display: flex;
      justify-content: space-around;
      color: @cCyan;
      font-size: 18px;
      .btns {
        cursor: pointer;
      }
      .bar {
        margin: 0 5px;
      }
    }

  }

  .right {
    width: 213px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .name {
      font-size: 24px;
    }
    .date {
      font-size: 18px;
      color: @cGray;
    }
    .errTxt {
      font-size: 18px;
      .status {
      }
      .reject {
        color: red;
      }
      .reviewing {
        color: @cOrange;
      }
      .pass {
        color: #13CE66;
      }
    }
  }
</style>



