<template>
  <div class="information">
    <div class="content">
      <ul>
        <li v-for="(item, index) in lists" @click="$router.push('/informationDetail/' + item.id)">
          <div class="itemContent">
            <img class="img" src="item.imgSrc">
            <div class="txtBox">
              <div class="txt">{{item.txt}}</div>
              <div class="time">发布日期：{{item.time}}</div>
            </div>
          </div>
        </li>
      </ul>
      <div class="pagination">
        <el-pagination
          layout="prev, pager, next"
          :page-size="12"
          :total="goodsTotal">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import choose1 from '../../assets/img/goods7.png';

  export default {
    data () {
      return {
        lists: [
          {imgSrc: choose1, txt: '号召力最新活动安排流程', time: '2017/09/23', id: 12138},
          {imgSrc: choose1, txt: '号召力最新活动安排流程', time: '2017/09/23'},
          {imgSrc: choose1, txt: '号召力最新活动安排流程', time: '2017/09/23'}
        ],
        goodsTotal: 120
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .content {
    margin-bottom: 100px;
  }

  .itemContent {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    margin-top: 50px;
    cursor: pointer;
    /*background-color: #59C1D2;*/
    .img {
      width: 350px;
      height: 260px;
      margin-right: 30px
    }
    .txtBox {
      width: 100%;
      height: 260px;
      position: relative;
      .txt {
        height: 93%;
        word-break: break-all;
        font-size: 20px;
        overflow: hidden;
      }
      .time {
        position: absolute;
        font-size: 17px;
        bottom: 0;
        right: 0;
        color: @cGray;
      }
    }
  }

  .pagination {
    width: 300px;
    margin: 50px auto 0 auto;
  }

</style>



