<template>
  <div class="more">
    <div class="content">
      <img class="img" src=""/>
      <ul class="ul">
        <li v-for="( item, index ) in list">
          <div class="itemContent">
            <img class="itemImg" src=""/>
            <div>{{item.txt}}</div>
          </div>
        </li>
      </ul>
      <div class="pagination">
        <el-pagination
          layout="prev, pager, next"
          :page-size="12"
          :total="goodsTotal">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        list: [
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'},
          {img: '', txt: '077'}
        ],
        goodsTotal: 120
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">

  .content{
    width: 1200px;
    margin: 0 auto;
  }

  .img{
    width: 150px;
    height: 50px;
    margin-bottom: 40px;
    margin-top: 150px;
  }

  .ul{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .itemContent{
      text-align: center;
      margin-bottom: 40px;
      .itemImg{
        width: 380px;
        height: 380px;
      }
    }
  }

  .pagination {
    width: 300px;
    margin: 50px auto 200px auto;
  }

</style>



