<template>
  <div class="inp-line">
    <slot name="left"></slot>
    <!--文本inp-->
    <input v-if="baseInp" :disabled="disabled"
           :type="inpType" :placeholder="placeTxt"
           :value="value" @input="updateValue($event.target.value)">
    <el-autocomplete v-else
                     v-model="searchItem"
                     :fetch-suggestions="querySearch"
                     :placeholder="placeTxt"
                     @select="handleSelect"
    ></el-autocomplete>
    <slot name="right"></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      baseInp: {
        type: Boolean,
        default: true // 是否为常规输入框
      },
      disabled: {
        type: Boolean,
        default: false  // 是否可修改
      },
      placeTxt: {
        type: String,
        default: ''
      },
      inpType: {
        type: String,
        default: 'text' // inp的type——text、password等
      },
      searchList: {
        type: Array // 搜索待选列表
      },
      value: {}
    },
    data () {
      return {
        list: [],  // 搜索待选列表
        searchItem: this.value  // 输入框数据
      };
    },
    watch: {
      searchList (newVal) {
        this.list = newVal; // 及时准确地在回调后部署数据
      },
      searchItem (newVal) {
        this.updateValue(newVal);
      }
    },
    methods: {
      updateValue (value) {
        var formattedValue = value;
        // 通过 input 事件发出数值
        this.$emit('input', formattedValue);
      },
      querySearch (queryString, cb) {
        var list = this.list;
        var results = queryString ? list.filter(this.createFilter(queryString)) : list;
        // 调用 callback 返回建议列表的数据
        console.log(results);
        cb(results);
      },
      createFilter (queryString) {
        return (restaurant) => {
          return (restaurant.value.indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect (item) {
        this.$emit('seleBank', item);
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .inp-line {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    height: 64px;
    border: 1px solid #959595;
    margin-bottom: 20px;
    overflow: hidden;
    line-height: 64px;
    font-size: 24px;
  }

  input {
    display: inline-block;
    width: 10px;
    flex: 1 1 10px;
    height: 64px;
    padding: 0 1em;
    border: none;
    font-size: 24px;
  }
</style>



