import Personal from '../../../views/personal/info/info.vue';
import Essential from '../../../views/personal/info/essential.vue';
import Message from '../../../views/personal/info/message.vue';
import Perfect from '../../../views/personal/info/perfect.vue';

export default {
  path: 'info',
  component: Personal,
  redirect: 'info/essential',
  children: [
    {
      path: 'essential',
      component: Essential
    },
    {
      path: 'message',
      component: Message
    },
    {
      path: 'perfect',
      component: Perfect
    }
  ]
};



