<template>
  <div class="goods" id="goods">
    <div class="content">
      <item-wrap v-for="(item, index) in dataList" :key="item.pk"
                 :itemData="item">
      </item-wrap>
      <div class="pagination">
        <el-pagination
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="goodsTotal"
          @current-change="reloadData">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import itemWrap from '../components/goods/itemWrap.vue';

  export default {
    components: {itemWrap},
    mounted () {
      this.initData();
    },
    data () {
      return {
        dataList: [
          // 指定个数的数据列表
          {
            name: null, // 商品名
            price: null, // 价格
            introduction: null,  // 介绍
            pic_file: null  // 图片
          }
        ],
        pageSize: 10,  // 每页的数据个数
        goodsTotal: 120 // 数据总个数
      };
    },
    methods: {
      initData () {
        this.ajaxData(0);
      },
      reloadData (index) {
        this.ajaxData(index - 1);
      },
      ajaxData (index) {
        Ajax.get({api: 'upload_good', index: index}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.dataList = data.list;  // 覆盖数据
            this.goodsTotal = data.total;
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .el-pagination {
    text-align: center;
  }
</style>



