<template>
  <div class="business">
    <div class="content">
      <item-wrap v-for="(item, index) in dataList" :key="item.tel">
      </item-wrap>
      <div class="pagination">
        <el-pagination
          layout="prev, pager, next"
          :page-size="this.pageSize"
          :total="goodsTotal"
          :current-change="reloadData">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import itemWrap from '../../components/business/item-wrap.vue';

  export default {
    components: {itemWrap},
    mounted () {
      this.ajaxData();
    },
    data () {
      return {
        dataList: [
          // 指定个数的数据列表
          {
            name: null,  // 名字
            address: null,  // 地址
            tel: null, // 电话
            industry: null, // 行业
            feature: null // 特色
          }
        ],
        pageSize: 10,  // 每页的数据个数
        goodsTotal: 120 // 数据总个数
      };
    },
    methods: {
      initData () {
        this.ajaxData(0);
      },
      reloadData (index) {
        this.ajaxData(index);
      },
      ajaxData (index) {
        let data = {
          index: index,  // 页码
          pageSize: this.pageSize // 单次请求个数
        };
        this.$http.get('', {params: {data}}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.dataList;
            this.goodsTotal;
//            data.list // 指定个数的数据列表
//            data.list.name
//            data.list.address
//            data.list.tel // 电话
//            data.list.industry // 行业
//            data.list.feature // 特色
//            data.total  // 数据总个数
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .content {
    width: 1200px;
    margin: 0 auto;
  }

  .pagination {
    width: 300px;
    margin: 80px auto 200px auto;
  }

</style>



