<template>
  <div class="personalMessage" style="line-height: 150%">
    <div class="content">
      <div class="message">
        会员等级：{{info.level}}<br/>
        号召力账号：{{info.account}}<br/>
        注册时间：{{info.regTime}}<br/>
        真实姓名：{{info.realName || '暂无'}}<br/>
        手机号码：{{info.mobile}}<br/>
        身份证号：{{info.ide || '暂无'}}<br/>
        户口地址：{{info.homeAddress || '暂无'}}<br/>
        现居地址：{{info.address || '暂无'}}<br/>
      </div>
      <div class="divider"></div>
      <div class="occupation">
        职业：{{info.occ || '暂无'}}<br/>
        个人爱好：{{info.hobby || '暂无'}}<br/>
        Q Q：{{info.qq || '暂无'}}<br/>
        微信：{{info.wx || '暂无'}}<br/>
        邮箱：{{info.email || '暂无'}}<br/>
      </div>
      <div class="divider"></div>
      <div class="emergency-line">
        <div class="urgent">紧急联系人：{{info.eme.urgent || '暂无'}}</div>
        <div class="rel">关系：{{info.eme.rel || '暂无'}}</div>
        <div class="mobile">手机号码：{{info.eme.mobile || '暂无'}}</div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';

  export default {
    mounted () {
      this.initInfo();  // 初始化详细信息
    },
    data () {
      return {
        info: {
          level: null,
          account: null,
          regTime: null,
          realName: null,
          mobile: null,
          ide: null,
          homeAddress: null,
          address: null,
          occ: null,
          hobby: null,
          qq: null,
          wx: null,
          email: null,
          eme: {
            urgent: '',
            rel: null,
            mobile: null
          }
        }
      };
    },
    methods: {
      initInfo () {
        Ajax.get({api: 'show_personal_info'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.info.level = data.info_msg.level;
            this.info.account = data.info_msg.account;
            this.info.regTime = data.info_msg.create_time;
            this.info.mobile = data.info_msg.mobile;
            this.info.ide = data.info_msg.id_card;
            this.info.occ = data.info_msg.job;
            this.info.hobby = data.info_msg.hobby;
            this.info.qq = data.info_msg.qq;
            this.info.wx = data.info_msg.wechat;
            this.info.email = data.info_msg.email;
            this.info.eme.urgent = data.info_msg.emergency_contact_name;
            this.info.eme.rel = data.info_msg.emergency_contact_relation;
            this.info.eme.mobile = data.info_msg.emergency_contact_mobile;
          }
        });
      }
    }
  }
  ;
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .content {
    font-size: 23px;
    margin-bottom: 100px;
    .message, .occupation, .emergency-line {
      color: @cLight;
      line-height: 200%;
    }
    .divider {
      width: 100%;
      height: 1px;
      background-color: #2ca3b6;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  .emergency-line {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    .binding, .unbundling {
      padding-left: 10px;
      padding-right: 10px;
      cursor: pointer;
    }
    .btns-box {
      .btns {
        cursor: pointer;
      }
      .change {
        color: @cCyan;
      }
      .del {
        color: @cOrange;
      }
      .bar {
        margin: 0 5px;
      }
    }
    .urgent, .rel, .mobile {
      margin-right: 50px;
    }
  }
</style>



