<template>
  <div class="bank">
    <small-title txt="银行账户绑定" color="#59C1D2"></small-title>
    <form>
      <inp-line placeTxt="请输入您所办理账户的真实姓名" v-model="formData.name"
                v-validate="'required'" name="真实姓名">
        <span slot="left" class="left">真实姓名</span>
      </inp-line>
      <inp-line placeTxt="请输入您的银行数字号码"
                v-model="formData.bankAccount"
                v-validate="'required'" name="银行账号">
        <span slot="left" class="left">银行账号</span>
      </inp-line>
      <inp-line placeTxt="请输入银行名称"
                :baseInp="false" :searchList="bankList"
                @seleBank="setBankSele"
                v-model="formData.bankName"
                v-validate="'required'" name="银行名称">
        <span slot="left" class="left">银行名称</span>
      </inp-line>
      <inp-line placeTxt="请输入18位身份证号码"
                v-model="formData.idCode"
                v-validate="'required|min: 18|max: 18'"
                name="身份证号码">
        <span slot="left" class="left">身份证号码</span>
      </inp-line>
      <!--<inp-line placeTxt="请输入您的6位银行密码" inpType="password"-->
      <!--v-model="formData.bankPwd"-->
      <!--v-validate="'required'" name="银行密码">-->
      <!--<span slot="left" class="left">银行密码</span>-->
      <!--</inp-line>-->
      <inp-line placeTxt="请设置支付的数字密码" inpType="password"
                v-model="formData.pwd"
                v-validate="'required'" name="支付密码">
        <span slot="left" class="left">支付密码</span>
      </inp-line>
      <inp-line placeTxt="请确认设置支付的数字密码" inpType="password"
                v-model="formData.pwd2"
                v-validate="'required'" name="确认密码">
        <span slot="left" class="left">确认密码</span>
      </inp-line>
      <inp-line placeTxt="请输入您银行绑定的手机号" v-model="formData.phone"
                v-validate="'required|mobile'" name="手机号码">
        <span slot="left" class="left">手机号码</span>
      </inp-line>
      <inp-line placeTxt="请输入图形验证码" v-model="formData.imgCode"
                v-validate="'required'" name="图形验证码">
        <span slot="left" class="left">图形验证码</span>
        <img slot="right" class="right imgCode" :src="imgCodeSrc" alt="...">
      </inp-line>
      <inp-line placeTxt="手机验证码" v-model="formData.msgCode"
                v-validate="'required'" name="手机验证码">
        <span slot="left" class="left">手机验证码</span>
        <div slot="right" class="right" @click="getMsgCode">获取验证码</div>
      </inp-line>
      <div class="submit" @click="getBind">确定绑定</div>
      <div class="back" @click="$router.go(-1)">&lt;返回</div>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import smallTitle from '../../components/common/smallTitle.vue';
  import inpLine from '../../components/common/inpLine.vue';

  export default {
    components: {smallTitle, inpLine},
    mounted () {
      this.initBankList();  // 初始化银行列表与搜索inp
    },
    data () {
      return {
        formData: {
          name: null,
          bankAccount: null,
          bankName: null,
          idCode: null, // 身份证
          bankPwd: 0,  // 银行密码（固定传0值）
          pwd: null,  // 支付密码
          pwd2: null, // 确认支付密码
          phone: null,  // 银行的手机号码
          imgCode: null,
          msgCode: null
        },
        bankList: [],  // 可选银行信息
        bankSele: {}  // 已经选择的银行
      };
    },
    computed: {
      imgCodeSrc () {
        if (this.formData.phone && !this.errors.has('手机号码')) {
          return 'http://api.haozhaoli510.com/authCode?mobile=' + this.formData.phone;
        } else {
          return;
        }
      }
    },
    methods: {
      initBankList () {
        Ajax.get({api: 'bank_bind'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            data.bank_list.forEach(item => {
              item.value = item.fields.name;
            });
            this.bankList = data.bank_list;
          }
        });
      },
      setBankSele (item) {
        // inp已经选择银行了，初始化bankSele
        this.bankSele = item;
      },
      getMsgCode () {
        // 获取短信验证码
        let data = {
          mobile: this.formData.phone,
          imageCode: this.formData.imgCode,
          type: '4' // 1 注册 2 积分提现 3 金币提现 4 绑定银行卡
        };
        Ajax.save({api: 'send_msg'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({title: '短信发送中', type: 'success'});
          }
        });
      },
      getBind () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            if (this.formData.pwd === this.formData.pwd2) {
              this.ajaxGetBind();  // 表单验证成功后ajax
            } else {
              this.$alert('设置的支付密码不一致！', '填写错误');
            }
          } else {
            this.$alert(this.errors.all()[0], '填写错误');  // 验证失败提示
          }
        });
      },
      ajaxGetBind () {
        let data = {
          bank_id: this.bankSele.pk,
          real_name: this.formData.name,
          bank_account: this.formData.bankAccount,
          bank_name: this.formData.bankName,
          id_card: this.formData.idCode,
          bank_password: this.formData,
          pay_password: this.formData.pwd,
          bank_mobile: this.formData.phone,
          check_code: this.formData.msgCode
        };
        Ajax.save({api: 'bank_bind'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({message: '绑定银行卡成功', type: 'success'});
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .bank {
    width: 585px;
    margin: 40px auto 0;
  }

  .smallTitle {
    margin-bottom: 40px;
  }

  form {
    width: 100%;
    .inp-line {
      .left {
        box-sizing: border-box;
        width: 155px;
        padding: 0 20px;
        text-align-last: justify;
      }
      .right {
        padding: 0 10px;
        border-left: 1px solid #959595;
        cursor: pointer;
      }
      .imgCode {
        width: 5em;
        padding: 0;
        text-align: center;
      }
    }
    .submit {
      width: 100%;
      height: 64px;
      text-align: center;
      background-color: @cCyan;
      cursor: pointer;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
    .back {
      margin: 10px 0 100px;
      text-align: center;
      font-size: 24px;
      color: @cCyan;
    }
  }
</style>



