<template>
  <div class="brief">
    <div class="content">
      <a-map></a-map>
      <h3>广州大学城 国家数字家庭研发区（东区）</h3>
      <div class="detailTxt">
        <p>广州大手互联网科技有限公司，旗下的“号召力”APP，卖家可以通过上传标签信息，让买家直接搜索到卖家的联系方式。
        “号召力”重新定义了人的价值，只要能提供服务即为商家，真正把人的价值深度发掘并共享出来。“号召力”APP通过
        用户上传个人标签，用户与用户之间相互服务，不仅解决了商业双向性的问题，也解决了传统商业模式中企业服务个人的
        单一性，增加了个人对个人，个人对企业，企业对企业，企业对个人等多种模式。</p>
        <p>秉承着“为人名服务”的价值观，公司推出全国首家公益共享单车“号召力”，零押金、免租费、免开锁、真正做到想骑
        就骑随停随用。通过这种颠覆传统的方式，迅速整合并占领共享单车市场，当用户量达到一定寄出后，商业性质的电话沟
          通必定存在大量交易，为公司未来实现金融帝国奠定基础。</p>
        2017年4月21日，广州大手互联网科技有限公司成立。<br/>
        2017年5月3日，旗下“号召力”公益共享单车在广州和深圳地区开始投放到市场。<br/>
        2017年5月19日，“号召力”APP发布beta1.0。<br/>
        2017年6月9日，“号召力”发布1.0公测版，包括微信版，安卓客户端，IOS客户端，各大电子应用市场均有上线。
      </div>

      <img class="img" src="公司风采">
      <div class="more" @click="$router.push('/briefMore')">更多 >></div>

      <div class="moreImg">
        <ul class="ul">
          <li v-for="(item, index) in imgList">
            <img class="itemImg" src="item.img">
          </li>
        </ul>
        <img class="rightImg" src="">
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import aMap from '../../components/brief/aMap.vue';
  export default {
    components: {aMap},
    data () {
      return {
        imgList: [
          {img: ''},
          {img: ''},
          {img: ''},
          {img: ''},
          {img: ''},
          {img: ''}
        ]
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">

  .content{
    width: 1200px;
    margin: 0 auto;
    margin-bottom: 100px;
    h3{
      margin: 15px;
      text-align: center;
    }
    .detailTxt{
      font-size: 20px;
      text-indent: 2em;
      line-height: 35px;
      p{
        margin-bottom: 20px;
      }
    }
    .img{
      width: 150px;
      height: 50px;
      margin-top: 180px;
    }
    .more{
      text-align: right;
      font-size: 18px;
      margin-top: 10px;
      margin-bottom: 20px;
      cursor: pointer;
    }
    .moreImg{
      width: 100%;
      display: flex;
      .ul{
        width: 1000px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-right: 40px;
      }
      .itemImg{
        width: 215px;
        height: 215px;
        display: flex;
        margin-bottom: 25px;
      }
      .rightImg{
        width: 455px;
        height: 455px;
      }
    }

  }




</style>



