<template>
  <div class="essential">
    <div class="content">
      <div class="message">
        会员等级：{{info.level}}<br/>
        号召力账号：{{info.account}}<br/>
        注册时间：{{info.regTime}}<br/>
        真实姓名：{{info.realName || '暂无'}}<br/>
        手机号码：{{info.mobile}}<br/>
        身份证号：{{info.ide || '暂无'}}<br/>
      </div>
      <div class="integral">
        <div class="available">可用积分：{{info.available}}</div>
        <div class="convertible">
          可兑换积分：{{info.convertible}}
          <div class="cWithdrawals" @click="$router.push('/mission/get')">
            提现
          </div>
        </div>
      </div>
      <div class="recharge">
        充值金额：{{info.rechargeAmount}}
        <div class="rWithdrawals">
          提现
        </div>
      </div>
      <div class="bank-box">
        <div class="bank">银行账号：{{info.bankAccount || '暂无'}}</div>
        <div class="btn-box">
          <span class="btns binding"
                @click="$router.push('/personal/bank')">绑定</span>
          <span class="bar">|</span>
          <span class="btns unbinding">解绑</span>
        </div>
      </div>
      <div class="btns-line">
        <div class="btns recharge">充 值</div>
        <div class="btns transferAccounts">转 账</div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';

  export default {
    mounted () {
      this.initInfo();  // 初始化信息
    },
    data () {
      return {
        info: {
          level: '', // 用户等级
          account: 'HZL0123',
          regTime: '2017-10-20', // 注册时间
          realName: '李思雨',
          mobile: '13565487455',
          ide: '123456789123456789',  // 身份证号
          available: 0, // 可用积分
          convertible: 0, // 可兑换积分
          rechargeAmount: 0,  // 充值金额
          bankAccount: null // 银行账户
        }
      };
    },
    methods: {
      initInfo () {
        Ajax.get({api: 'show_base_info'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.info.level = data.info_msg.level;
            this.info.account = data.info_msg.account;
            this.info.regTime = data.info_msg.create_time;
            this.info.realName = data.info_msg.real_name;
            this.info.mobile = data.info_msg.mobile;
            this.info.ide = data.info_msg.id_card;
            this.info.available = data.info_msg.avaliable_intergral;
            this.info.convertible = data.info_msg.intergral_num;
            this.info.rechargeAmount = data.info_msg.gold_num;
            this.info.bankAccount = data.info_msg.bank_account;
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .essential {
    line-height: 150%;
  }

  .content {
    font-size: 23px;
    color: #606060;
    .message {
      line-height: 200%;
      .account {
        font-size: 19px;
      }
    }
    .integral {
      display: flex;
      margin-top: 5px;
      margin-bottom: 15px;
      align-items: center;
      .available {
        margin-right: 50px;
      }
      .convertible {
        display: flex;
        align-items: center;
        .cWithdrawals {
          margin-left: 20px;
          background-color: @cCyan;
          padding: 10px 20px;
          color: @cWhite;
          cursor: pointer;
        }
      }
    }
    .bank-box {
      display: flex;
      margin-top: 20px;
      .bank {
        margin-right: 50px;
      }
      .btn-box {
        .bar {
          margin: 0 5px;
        }
        .btns {
          padding-left: 10px;
          padding-right: 10px;
          cursor: pointer;
        }
        .binding {
          color: @cCyan;
        }
        .unbinding {
          color: @cOrange;
        }
      }
    }
    .recharge {
      display: flex;
      align-items: center;
      .rWithdrawals {
        margin-left: 20px;
        background-color: @cCyan;
        padding: 10px 20px;
        color: @cWhite;
        cursor: pointer;
      }
    }
  }

  .btns-line {
    width: 100%;
    margin: 80px auto 100px;
    display: flex;
    .btns {
      padding: 23px 100px;
      margin: 10px;
      background-color: @cCyan;
      color: white;
    }
  }
</style>



