<template>
  <div class="points">
    <small-title txt="积分提现" color="#59C1D2"></small-title>
    <form>
      <inp-line placeTxt="可兑换积分提现" v-model="formData.points"
                :disabled="true"
                v-validate="'required'" name="积分提现">
        <span slot="left" class="left">积分提现</span>
      </inp-line>
      <inp-line placeTxt="请输入银行数字账号"
                :disabled="true"
                v-model="formData.bankAccount"
                v-validate="'required'" name="银行账户">
        <span slot="left" class="left">银行账户</span>
      </inp-line>
      <inp-line placeTxt="请输入银行名称"
                :disabled="true"
                v-model="formData.bankName"
                v-validate="'required'" name="银行名称">
        <span slot="left" class="left">银行名称</span>
      </inp-line>
      <inp-line placeTxt="请输入你设置的支付密码" inpType="password"
                v-model="formData.pwd" v-validate="'required'" name="支付密码">
        <span slot="left" class="left">支付密码</span>
      </inp-line>
      <inp-line placeTxt="建议使用常用手机" v-model="formData.phone"
                v-validate="'required|mobile'" name="手机号码">
        <span slot="left" class="left">手机号码</span>
      </inp-line>
      <inp-line placeTxt="请输入验证码" v-model="formData.imgCode"
                v-validate="'required'" name="验证码">
        <span slot="left" class="left">验证码</span>
        <img slot="right" class="right imgCode" :src="imgCodeSrc" alt="...">
      </inp-line>
      <inp-line placeTxt="手机验证码" v-model="formData.msgCode"
                v-validate="'required'" name="手机验证码">
        <span slot="left" class="left">手机验证码</span>
        <div slot="right" class="right" @click="getMsgCode">获取验证码</div>
      </inp-line>
      <div class="submit" @click="getCash">确定提现</div>
      <div class="back" @click="$router.go(-1)">&lt;返回</div>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import smallTitle from '../../components/common/smallTitle.vue';
  import inpLine from '../../components/common/inpLine.vue';

  export default {
    components: {smallTitle, inpLine},
    mounted () {
      this.initParams();  // 路由参数初始化数据
      this.initBankInfo();  // 初始化路由信息
    },
    data () {
      return {
        formData: {
          points: 0,  // 积分
          id: null, // 任务ID
          bankAccount: null,  // 银行账户
          bankName: null, // 银行名称
          pwd: null,  // 支付密码
          phone: null,  // 手机号码
          imgCode: null,  // 图形验证码
          msgCode: null // 短信验证码
        }
      };
    },
    computed: {
      imgCodeSrc () {
        if (this.formData.phone && !this.errors.has('手机号码')) {
          return 'http://api.haozhaoli510.com/authCode?mobile=' + this.formData.phone;
        } else {
          return;
        }
      }
    },
    methods: {
      initParams () {
        let params = this.$route.params;
        this.formData.points = params.completed;
        this.formData.id = params.id;
      },
      initBankInfo () {
        Ajax.get({api: 'intergral_withdraw'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.formData.bankAccount = data.bank_account;
            this.formData.bankName = data.bank_name;
          }
        });
      },
      getMsgCode () {
        // 获取短信验证码
        let data = {
          mobile: this.formData.phone,
          imageCode: this.formData.imgCode,
          type: '3' // 1 注册 2 积分提现 3 金币提现
        };
        Ajax.save({api: 'send_msg'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({title: '短信发送中', type: 'success'});
          }
        });
      },
      getCash () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.ajaxGetCash();  // 表单验证成功后ajax
          } else {
            this.$alert(this.errors.all()[0], '填写错误');  // 验证失败提示
          }
        });
      },
      ajaxGetCash () {
        let data = {
          bank_account: this.formData.bankAccount,
          bank_name: this.formData.bankName,
          pay_pwd: this.formData.pwd,
          mobile: this.formData.phone,
          check_msg: this.formData.msgCode,
          task_id: this.formData.id
        };
        Ajax.save({api: 'intergral_withdraw'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({message: '提现成功', type: 'success'});
            this.$router.push('/home');
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .points {
    width: 585px;
    margin: 40px auto 0;
  }

  .smallTitle {
    margin-bottom: 40px;
  }

  form {
    width: 100%;
    .inp-line {
      .left {
        box-sizing: border-box;
        width: 155px;
        padding: 0 20px;
        text-align-last: justify;
      }
      .right {
        padding: 0 10px;
        border-left: 1px solid #959595;
        cursor: pointer;
      }
      .imgCode {
        width: 5em;
        padding: 0;
        text-align: center;
      }
    }
    .submit {
      width: 100%;
      height: 64px;

      text-align: center;
      background-color: @cCyan;
      cursor: pointer;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
    .back {
      margin: 10px 0 100px;
      text-align: center;
      font-size: 24px;
      color: @cCyan;
    }
  }
</style>



