<template>
  <div class="errsDetail">
    <div class="item-wrap">
      <div class="left">
        <img class="leftImg"
             :src="formData.fields.pic_file"
             alt="请上传图片！">
        <div class="btn-line">
          <div class="loadBtn">
            图片上传
            <input type="file" class="imgInp" @change="onPhoto"
                   accept="image/jpg,image/jpeg,image/png">
          </div>
          <i class="bar">|</i>
          <div class="delBtn" @click="ajaxDel">删除选项</div>
        </div>
      </div>
      <div class="right">
        <form>
          <inp-line placeTxt="请输入商家名" v-model="formData.fields.name"
                    v-validate="'required'" name="名称"
                    :class="{danger: errors.has('名称')}">
            <div slot="left" class="inp-left">名称</div>
          </inp-line>
          <inp-line placeTxt="请输入详细地址"
                    v-model="fixed.detail"
                    v-validate="'required'" name="地址"
                    :class="{danger: errors.has('地址')|| formData.fields.seleAddress === null}">
            <div slot="left" class="inp-left-line">
              <span class="inp-left">地址</span>
              <el-cascader :options="addressList"
                           @active-item-change="getMoreAddress"
                           :props="cascaderInit"
                           v-model="fixed.cascader">
              </el-cascader>
            </div>
          </inp-line>
          <inp-line placeTxt="请输入商家手机号或固定号码"
                    v-model="formData.fields.mobile"
                    v-validate="'required'" name="电话"
                    :class="{danger: errors.has('电话')}">
            <div slot="left" class="inp-left">电话</div>
          </inp-line>
          <inp-line placeTxt="请输入行业（如：超市、家居电器等）"
                    v-model="formData.fields.industry"
                    v-validate="'required'" name="行业"
                    :class="{danger: errors.has('行业')}">
            <div slot="left" class="inp-left">行业</div>
          </inp-line>
          <inp-line placeTxt="请输入主要出售的商品（如：空调、电视等）"
                    v-model="formData.fields.feature"
                    v-validate="'required'" name="特色"
                    :class="{danger: errors.has('特色')}">
            <div slot="left" class="inp-left">特色</div>
          </inp-line>
        </form>
      </div>
    </div>
    <div class="btn-line">
      <div class="btns cancel" @click="$router.go(-1)">返回</div>
      <div class="btns submit" @click="submit">确定上传</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import base64 from 'base-64';
  import utf8 from 'utf8';
  import inpLine from '../../components/common/inpLine.vue';

  export default {
    components: {inpLine},
    mounted () {
      this.initData();  // 初始化路由参数到组件
      this.initProList(); // 初始化省级列表
      this.initAddress(); // 初始化多级地址
      this.$nextTick(() => this.initVee()); // 验证表单
    },
    data () {
      return {
        formData: {fields: {address: ''}},  // 从路由参数得来
        addressList: [
          {
            label: '广东',
            children: [
              {
                label: '广州',
                children: [
                  {label: '天河'},
                  {label: '白云'}
                ]
              }
            ]
          }
        ],  // 多级联动地址列表
        fixed: {
          detail: '',
          cascader: [] // 多级联动已选
        },
        cascaderInit: {value: 'label'}  // 多级联动设置
      };
    },
    methods: {
      initData () {
        this.formData = JSON.parse(utf8.decode(base64.decode(this.$route.params.obj_formData)));  // 解密路由参数
      },
      initProList () {
        this.ajaxProList();
      },
      initAddress () {
        this.fixed.detail = this.formData.fields.address.split('-')[1];
        this.fixed.cascader = this.formData.fields.address.split('-')[0].split(',');
      },
      initVee () {
        this.$validator.validateAll();
      },
      onPhoto (index) {
        // 获取图片
        let inp = document.querySelector('.imgInp');
        if (inp.files && inp.files[0]) {
          // console.log(inp.files[0].size);
          this.condense(inp.files[0], index);
        }
      },
      condense (file, index) {
        // 压缩图片
        let that = this;
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function (e) {
          var img = new Image();
          var width = 355;  // image resize
          var quality = 1;  // image quality
          var canvas = document.createElement('canvas');
          var drawer = canvas.getContext('2d');
          img.src = this.result;
          img.onload = function () {
            canvas.width = width;
            canvas.height = width * (img.height / img.width);
            drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
            let resImg = canvas.toDataURL('image/jpeg', quality);  // 压缩完毕的图片
            that.formData.fields.pic_file = resImg;
          };
        };
      },
      getMoreAddress (val) {
        // val === [省、市、区]
        if (val.length === 1) {
          // 获取市级
          for (let key of Object.keys(this.addressList)) {
            if (val[0] === this.addressList[key].label) {
              this.ajaxCityList(
                this.addressList[key].label,
                (val) => {
                  this.addressList[key].children = val;
                });
              return;
            }
          }
        } else if (val.length === 2) {
          // 获取区级
          for (let key of Object.keys(this.addressList)) {
            if (val[0] === this.addressList[key].label) {
              for (let key2 of Object.keys(this.addressList[key].children)) {
                if (val[1] === this.addressList[key].children[key2].label) {
                  this.ajaxAreaList(
                    this.addressList[key].children[key2].label,
                    (val) => {
                      this.addressList[key].children[key2].children = val;
                    });
                  return;
                }
              }
              return;
            }
          }
        }
      },
      submit () {
        this.ajaxSubmit();
      },
      ajaxProList () {
        // 省级列表
        Ajax.get({api: 'get_task_province'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.province_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({children: []});
              fixed[index].label = item;
            });
            this.addressList = fixed;
          }
        });
      },
      ajaxCityList (data, reback) {
        // 市列表
        Ajax.save({api: 'get_task_city'}, {province: data}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.cities_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({children: []});
              fixed[index].label = item.fields.num;
            });
            reback(fixed);
          }
        });
      },
      ajaxAreaList (data, reback) {
        // 市、区列表
        Ajax.save({api: 'get_task_area'}, {city: data}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.areas_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({});
              fixed[index].label = item.fields.areaname;
            });
            reback(fixed);
          }
        });
      },
      ajaxSubmit () {
        let fixedAddress = this.fixed.cascader + '-' + this.fixed.detail;
        let data = {
          content_id: this.formData.pk,
          industry: this.formData.fields.industry,
          mobile: this.formData.fields.mobile,
          name: this.formData.fields.name,
          address: fixedAddress,
          pic_file: this.formData.fields.pic_file,
          feature: this.formData.fields.feature
        };
        Ajax.save({api: 'edit_upload_data'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify.success({title: '上传成功'});
            this.$router.push('/mission/errs');
          } else {
            this.$notify.error({title: '上传失败！'});
          }
        });
      },
      ajaxDel () {
        // 删除错误
        let data = {content_id: this.formData.pk};
        Ajax.save({api: 'delete_upload_data'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify.success({title: '删除成功！'});
            this.$router.push('/mission/errs');
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .errsDetail {

  }

  .item-wrap {
    width: 1200px;
    margin: 50px auto;
    display: flex;
    justify-content: space-between;
  }

  .left {
    width: 355px;
    img {
      display: inline-block;
      box-sizing: border-box;
      width: 355px;
      height: 355px;
      text-align: center;
      line-height: 355px;
      font-size: 24px;
      border: 1px solid @cBorder;
    }
    .danger {
      border: 1px solid red; // 表单验证失败
    }
    .btn-line {
      width: 100%;
      margin-top: 18px;
      display: flex;
      justify-content: center;
      font-size: 24px;
      color: @cCyan;
      .loadBtn, .delBtn {
        position: relative;
        display: inline-block;
        width: 4em;
        cursor: pointer;
        .imgInp {
          position: absolute;
          top: 0;
          left: 0;
          width: 4em;
          opacity: 0;
        }
      }
      .bar {
        display: inline-block;
        margin: 0 10px;
      }
    }
  }

  .right {
    width: 710px;
    form {
      .inp-left {
        display: inline-block;
        width: 4em;
        text-align: center;
        &::after {
          content: '|';
          margin: 0 5px 0 15px;
        }
      }
      input {
        padding: 0;
      }
      .danger {
        border: 1px solid red; // 表单验证失败
      }
    }
  }

  .btn-line {
    width: 500px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    .btns {
      width: 175px;
      height: 64px;
      background-color: @cCyan;
      cursor: pointer;
      text-align: center;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
  }
</style>



