<template>
  <div class="errs">
    <div class="content">
      <div class="top-line">
        <div class="missionBtn"
             @click="$router.go(-1)">返回
        </div>
      </div>
      <ul class="errs-wrap">
        <errs-item v-for="(item,index) in errsList" :key="item.pk"
                   :formData="item"
                   @delErrItem="updateList"></errs-item>
      </ul>
    </div>
    <footer>
      <i class="red">*上传重复或信息有误商家共{{errsList.length}}个</i>
      ，将不纳入完成数量，请及时修改或删除
    </footer>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import errsItem from '../../components/mission/errs/errsItem.vue';

  export default {
    components: {errsItem},
    mounted () {
      this.initErrsList();  // 初始化错误列表
    },
    data () {
      return {
        errsList: []  // 错误信息列表
      };
    },
    methods: {
      initErrsList () {
        Ajax.get({api: 'show_wrong_data'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.errsList = data.list;
          }
        });
      },
      updateList (pk) {
        console.log(pk);
        for (let key of Object.keys(this.errsList)) {
          if (this.errsList[key].pk === pk) {
            this.errsList.splice(key, 1);
            return;
          }
        }
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .errs {
    padding-bottom: 100px;
  }

  .content {
    width: 1200px;
    margin: 0 auto;
  }

  .top-line {
    margin: 100px 0;
    display: flex;
    justify-content: space-between;
    align-content: center;
    .missionBtn {
      width: 128px;
      height: 36px;
      border-radius: 10px;
      background-color: @cCyan;
      box-shadow: 4.7px 3.8px 2px 0px rgba(0, 0, 0, 0.27),
      inset 9.7px 20.8px 2px 0px rgba(89, 193, 210, 0.39);
      cursor: pointer;
      text-align: center;
      line-height: 36px;
      font-size: 24px;
      color: @cWhite;
    }
    .date-box {
      font-size: 20px;
      .el-date-picker {
      }
    }
  }

  .errs-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .errsItem {
      width: 40%;
    }
  }

  footer {
    margin-top: 100px;
    text-align: center;
    font-size: 18px;
    .red {
      color: red;
    }
  }
</style>



