<template>
  <div class="itemWrap" v-if="itemData.fields">
    <div class="img-box">
      <img class="itemImg" :src="itemData.fields.pic_file">
    </div>
    <div class="info-box">
      名称-{{itemData.fields.name}}<br/>
      介绍-{{itemData.fields.introduction}}<br/>
      价格-{{itemData.fields.price}}<br/>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      itemData: {
        type: Object,
        default: () => {
          return {
            fields: {
              name: null, // 名字
              introduction: null, // 介绍
              price: null,  // 价格
              pic_file: null // 图片
            }
          };
        }
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .itemWrap {
    display: flex;
    margin-bottom: 40px;
  }

  .img-box {
    img {
      display: inline-block;
      width: 320px;
      height: 320px;
    }
  }

  .info-box, .btns-box {
    font-size: 20px;
    margin: auto 0;
    line-height: 40px;
  }

  .info-box {
    width: 630px;
    padding-left: 70px;
  }

  .btns {
    background-color: @cCyan;
    padding-left: 25px;
    padding-right: 25px;
    color: @cWhite
  }

  .distance {
    margin-bottom: 20px;
  }

  .navigation {
    cursor: pointer;
  }
</style>



