<template>
  <div class="selfHead">
    <div class="right">
      <div class="content-line">
        <div class="noLogin-line" v-if="!(account || userName)">
          <div class="txt">你好，</div>
          <router-link class="login" to="/login">请登陆</router-link>
          <router-link class="register" to="/register">免费注册</router-link>
        </div>
        <div class="logined-line" v-else>
          <span class="info" @click="$router.push('/personal/info')">{{userName || account}}</span>
          <span class="out" @click="out">登出</span>
        </div>
        <i class="line"></i>
        <router-link to="">我的订单</router-link>
        <i class="line"></i>
        <router-link to="">客户服务</router-link>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import Bus from '../../utils/bus.js';

  export default {
    created () {
      this.account = localStorage.getItem('account');
      this.userName = localStorage.getItem('userName');
      Bus.$on('logined', () => {
        this.account = localStorage.getItem('account');
        this.userName = localStorage.getItem('userName');
      });
    },
    data () {
      return {
        account: null,
        userName: null
      };
    },
    methods: {
      out () {
        Ajax.get({api: 'logout'}).then(res => {
          if (res.body.status) {
            this.$notify({
              title: '登出成功',
              type: 'success'
            });
            localStorage.clear();
            this.$router.push('/home');
            setTimeout(() => {
              location.reload();
            }, 500);
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../../assets/css/common.less';

  .selfHead {
    position: absolute;
    z-index: 100;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 1200px;
    margin: 0 auto 0;
    display: flex;
    flex-direction: row-reverse;
  }

  .right {
  }

  .content-line {
    height: 46px;
    padding: 6px 28px 0px;
    border: solid 1px @cCyan;
    border-top: none;
    border-radius: 0 0 10px 10px;
    box-shadow: 0.5px 0.9px 1px 0px #035C6B;
    background-color: @cWhite;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    color: @cLight;
    // 未登陆状态
    .noLogin-line {
      display: flex;
      .login {
        margin-right: 10px;
        color: @cCyan;
      }
      .register {
        color: @cCyan;
      }
    }
    // 登陆状态
    .logined-line {
      .info {
        cursor: pointer;
        &:hover {
          color: @cCyan;
        }
      }
      .out {
        cursor: pointer;
        color: @cCyan;
      }
    }
    .line {
      display: inline-block;
      width: 1px;
      height: 18px;
      margin: 0 10px;
      background-color: @cLight;
    }
  }
</style>



